import React, { Suspense } from 'react';
import { Route, Redirect, Switch } from 'react-router-dom';

const ViewHack = React.lazy(() =>
  import(/* wepackChunkName: "challenges-hack" */ './details')
);
const HackWizard = React.lazy(() =>
  import(/* wepackChunkName: "challenges-hack-wizard" */ './hack-wizard')
);
const ViewChallenges = React.lazy(() =>
  import(/* wepackChunkName: "challenges-view" */ './view-challenges')
);
const ExploreChallenges = React.lazy(() =>
  import(/* wepackChunkName: "challenges-view" */ './explore-challenges')
);
const SubmitChallenge = React.lazy(() =>
  import(/* wepackChunkName: "challenges-submission" */ './submission')
);

const Challenges = ({ match }) => (
  <Suspense fallback={<div className="loading" />}>
    <Switch>
      <Redirect exact from={`${match.url}/`} to={`${match.url}/view`} />
      <Route
        path={`${match.url}/view`}
        render={(props) => <ViewChallenges {...props} />}
      />
      <Route
        path={`${match.url}/explore`}
        render={(props) => <ExploreChallenges {...props} />}
      />
      <Route
        exact
        path={`${match.url}/:id`}
        render={(props) => <ViewHack {...props} />}
      />
      <Route
        exact
        path={`${match.url}/register/:id`}
        render={(props) => <HackWizard {...props} />}
      />
      <Route
        exact
        path={`${match.url}/:id?/submission`}
        render={(props) => <SubmitChallenge {...props} />}
      />
      <Redirect to="/error" />
    </Switch>
  </Suspense>
);

export default Challenges;
